import React, { Component } from 'react';
import styled from "styled-components";
import MusicPlayer from './MusicPlayer';
import Timer from './Timer';
import TodoList from './TodoList';
import TodoForm from './TodoForm';  
import {Link} from 'react-router-dom';
import '../css/Main.css';

const TodoListDiv = styled.div`
  /* background: black; */
  /* width: 50%; */
  margin-left: 80px;
  /* align-items: center; */
`

const StyledH1 = styled.h1`
  font-size: 45px;
  margin: 10px;
  color: var(--primary);
  /* text-shadow: -1px 0 #AE857C, 0 1px #AE857C, 1px 0 #AE857C, 0 -1px #AE857C; */
  text-align: center;
`

export default class Main extends Component {
    constructor(props){
        super(props);
        this.state = {
            expanded: "",
            chat: "",
            focusedTime: 0,
            playerReady: false,
            todo: '',
            todos: [{ // Array of our to-do items
                // task: '',
                // id: '',
                // completed: false
            }],
        }
    }

    componentDidMount = () =>{

        this.setState({ 
            expanded: document.querySelector(".bulk-container").classList.contains("active") ? true : false, 
            chat: document.getElementById("wrapper").classList.contains("toggled") ? true : false, 
            focusedTime: localStorage.getItem("focusedTime") ? localStorage.getItem("focusedTime") : 0
        });

        const button = document.getElementById("toggle");
        button.addEventListener("click", () => {
            const cont = document.querySelector(".bulk-container");
            cont.classList.toggle("active");
            this.setState({ expanded: document.querySelector(".bulk-container").classList.contains("active") ? true : false, });
        });

        const chatBtn = document.getElementById("chat-toggle");
        const wrapper = document.getElementById("wrapper");
        wrapper.classList.toggle("toggled");
        this.setState({ chat: document.getElementById("wrapper").classList.contains("toggled") ? false : true, });
        chatBtn.addEventListener("click", () => {
            wrapper.classList.toggle("toggled");
            this.setState({ chat: document.getElementById("wrapper").classList.contains("toggled") ? false : true, });
        });

        const send_btn = document.getElementById("send");
        var chat_list = document.getElementById("chat_list");
        var send_txt = document.getElementById("content");
        var history = "";
        send_btn.addEventListener("click", () => {
            if (send_txt.value != "") {
                var text = send_txt.value;
                text = text.replaceAll("&", "&amp;");
                text = text.replaceAll("<", "&lt;");
                text = text.replaceAll("\n", "<br>");
                history += "<p style=\"background-color: #11131b; color: #f7f7f7; padding: 10px; margin:10px; border-radius: 14px;\"><b>" + "USER NAME" + "</b><br>" + text + "</p>";
                chat_list.innerHTML = history;
                send_txt.value = "";
            }
        });

        // Disable console logs in production
        if(process.env.REACT_APP_DEBUG !== "TRUE"){
            if(!window.console) window.console = {};
            var methods = ["log", "debug", "warn", "info", "error"];
            for(var i=0;i<methods.length;i++){
                console[methods[i]] = function(){};
            }
        }

        this.addLocalStorage();
        // Calls saveLocalStorage upon the event of unloading our application, & makes sure it runs our it before we unload.
        window.addEventListener(
            "beforeunload",
            this.saveLocalStorage.bind(this)
        )
    }

    getFocusedTime = (time) =>{
        this.setState({focusedTime: time});
    }

    setPlayerStatus = status => {
        this.setState({playerReady:status});
      }

    inputChangeHandler = event => {
        this.setState({[event.target.name]: event.target.value})
    }

    addTask = event => {
        // preventDefault stops page from refreshing with every new to-do item
        event.preventDefault();
        // To-do item object
        let newTask = {
          task: this.state.todo,
          id: Date.now(), // Any unique identifier would work as well
          completed: false
        };
        // Adds newTask to the array of to-do items
        this.setState({
          todos: [...this.state.todos, newTask], // "..." is the spread operator to ensure there is only 1 array
          todo: '' // Resets todo so it can be filled again
        })
      }

    toggleComplete = itemId => {
        // Maps over todos array & changes completed value if id matches
        const todos = this.state.todos.map(todo => {
          if (todo.id == itemId) {
            todo.completed = !todo.completed
          }
          return todo
        });
        this.setState({todos,todo:''})
    }

      // Removes completed to-do items
    removeItems = event => {
        event.preventDefault();
        // Sets todos array to a new array of only non-completed items based off the previous state
        this.setState(prevState => {
        return {
            todos: prevState.todos.filter(todo => {
            return !todo.completed;
            })
        }
        })
    }

    // Removes ALL items
    removeAllItems = event => {
        event.preventDefault();
        this.setState({todos:[]})
    }

    addLocalStorage = () => {
        for (let key in this.state) { // Takes all of our items in state
          if (localStorage.hasOwnProperty(key)) { // Checks if each item in state is in our local storage
            let value = localStorage.getItem(key);
            try {
              value = JSON.parse(value); // Converts item strings to JavaScript objects
              this.setState({[key]:value}) // Renders the correct data to the screen
              console.log({[key]:value});
            }
            catch(event) {
              this.setState({[key]:value})
            }
          }
        }
      }
    
      // Adds Items in State to our Local Storage
      saveLocalStorage = () => {
        for (let key in this.state) {
          let value = JSON.stringify(this.state[key]); // Transforms any object/array into a string
          localStorage.setItem(key, value);
          console.log('hello');
          // console.log(value);
        }
      }


    render() {
        return (
            <div className={this.props.darkmode ? "container-fluid dark-mode" : "container-fluid light-mode"}>
                <div className="heading">
                    <h1>CC-Final Pomodoro</h1>
                    <div className="focusedTime">
                        <p>Total Focused Time</p>
                        <p>{this.state.focusedTime} minutes</p>
                    </div>
                    <button id="chat-toggle">
                        <i className="material-icons-round">{this.state.chat ? "arrow_back_ios" : "arrow_forward_ios"}</i>
                    </button>
                </div>
                <div className="bulk-container">
                    <button id="toggle">
                        <i className="material-icons-round">{this.state.expanded ? "chevron_left" : "queue_music"}</i>
                    </button>
                    <Timer sendFocusedTime={this.getFocusedTime} playerReady={this.state.playerReady}/>
                    <MusicPlayer setPlayerStatus = {this.setPlayerStatus}/>   
                </div>
                <div>
                    <TodoListDiv>
                    <StyledH1>Todo List</StyledH1>
                            <TodoList
                                todos={this.state.todos}
                                toggleComplete={this.toggleComplete}
                            />
                            <TodoForm
                                todos={this.state.todos}
                                value={this.state.todo}
                                inputChangeHandler={this.inputChangeHandler}
                                addTask={this.addTask}
                                removeItems={this.removeItems}
                                removeAllItems={this.removeAllItems}
                            />
                    </TodoListDiv>
                </div>
                <div className="footer">
                    <Link id="settings" to="/settings"><i className="material-icons-round">settings</i></Link>
                </div>
            </div>
        )
    }
}
